
.drag-and-drop-wrapper {
  cursor: grab;
  width: 100px;
  height: 100px;
}

.drag-enter {
  border: 2px dashed gray;
}

.row {
  box-sizing: border-box;
  padding: 5px;
  margin: 0;
  font-size: 14px;
}

.row:hover {
  background: #edf4fa;
  cursor: grab;
}

.row:active {
  cursor: grabbing;
}

.drag {
  display: inline-block;
  padding: 0 3px;
  cursor: grab;
}

.drag:active {
  cursor: grabbing;
}
